@use "./utils" as *;

main {
  max-width: toRem(1600);
}

.call-to-action-section {
  position: relative;
  min-height: 30rem;
  height: 60vh;
  width: 100%;
  @include ctasBgGradient;
}

.logo {
  color: $white;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  position: absolute;
  left: 1rem;
  top: 1rem;
}
